*, *::before, *::after {
    box-sizing: border-box;
}

* {
    margin: 0;
    padding: 0;
    font: inherit;
}

body {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

img, picture, svg, video {
    display: block;
    max-width: 100%;
}

/* --- Fonts & Custom Properties --- */
:root {
    --dark-mode-bg-color: hsl(0, 0%, 12%);
    --main-font-color: hsl(0, 0%, 100%);
    --secondary-font-color: hsl(0, 0%, 45%);
}

@font-face {
    font-family: "heading-font";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

@font-face {
    font-family: "body-font";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

/* --- Base --- */
body {
    display: grid;
    grid-template-rows: 10% 10% 60% 15% 5%;
    background-color: var(--dark-mode-bg-color);
}

h1, h2, h3 {
    color: var(--main-font-color);
    font-family: "heading-font";
}

p, a, input, img {
    color: var(--main-font-color);
    font-family: "body-font";
}

/* --- Main --- */
header {
    grid-row: 1;
    padding: 1rem 2rem;
}

header h1 {
    font-family: "heading-font";
    font-size: 3.5rem;
}

.searchbox-wrapper {
    grid-row: 2;
    display: flex;
    justify-content: center;
    margin: 1.5rem 0;
}

.searchbox {
    width: 80%;
    height: 40px;
    background-color: hsl(0, 0%, 16%);
    border-radius: 20px;
    position: relative;
    z-index: 1;
}

.searchbox > input {
    position: absolute;
    top: 0;
    left: 0;
    border: none;
    padding: 0 20px;
    width: 80%;
    height: 40px;
    background-color: hsl(0, 0%, 16%);
    border-radius: 20px;
    z-index: 10;
}

.searchbox > input:focus {
    outline: none;
}

.searchbox > input::placeholder {
    color: var(--secondary-font-color);
    font-family: "body-font";
}

.searchbox > .search-btn {
    font-family: "body-font";
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 20%;
    border: none;
    border-radius: 20px;
    color: var(--main-font-color);
    /* background: linear-gradient(to right, rgb(248,98,227), rgb(61,99,241)); */
    background: linear-gradient(to right, rgb(254,122,61), rgb(255,55,53));
    z-index: 100;
    cursor: pointer;
}

.dashboard {
    grid-row: 3;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.current-panel {
    position: absolute;
    top: 0;
    display: grid;
    grid-template-columns: 50% 50%;
    width: 90%;
    height: 38%;
    padding: 1rem;
    border-radius: 25px;
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.3);
    -webkit-backdrop-filter: blur(10px);
    margin-bottom: 1rem;
}

.icon {
    grid-column: 1;
}

.info {
    grid-column: 2;
}

.info__temp {
    font-size: 7rem;
}

.details-panel {
    position: absolute;
    bottom: 0;
    display: grid;
    place-items: center;
    grid-template-columns: repeat(3, 33%);
    grid-template-rows: repeat(2, 50%);
    width: 90%;
    height: 59%;
    padding: 1rem;
    border-radius: 25px;
    background-color: rgb(40,40,40);
}

.details-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.details-box > p:first-of-type {
    color: var(--main-font-color);
}

.details-box > p:last-child {
    color: var(--secondary-font-color);
}

.sun-info-wrapper {
    grid-row: 4;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sun-info {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 1rem;
    border-radius: 25px;
    background-color: rgb(40,40,40);
    width: 90%;
    height: 85%;
}

footer {
    grid-row: 5;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: auto;
    height: 100%;
}

.fa-square-github,
.fa-square-github:active,
.fa-square-github:visited, 
a {
    color: white;
    text-decoration: none;
